import {
    Panel,
    Button,
    Group,
    Div,
    Avatar,
    PanelHeader,
    HeaderButton,
    Cell,
    IOS,
    Spinner,
    Progress
} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import React from 'react';

import PropTypes from 'prop-types';

import {Friends, Users} from '../api';
import connect from '@vkontakte/vkui-connect-promise';

class VueFriends extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            usersInfo: [],
            isSpinner: true,
            countFriendsInstagram: 0,
        };
    }

    getFriends () {
        let offsetFriendsInterval = 0;
        this.friendsInterval = setInterval(() => {
            connect.send('VKWebAppCallAPIMethod', {
                    method: 'friends.get',
                    params: {
                        count: 100,
                        offset: offsetFriendsInterval,
                        fields: 'nickname, domain, sex, bdate, city, country, timezone, photo_50, photo_100, photo_200_orig, has_mobile, contacts, education, online, relation, last_seen, status, can_write_private_message, can_see_all_posts, can_post, universities ',
                        access_token: this.props.access_token,
                        v: '5.95'
                    }
                })
                .then(res => {
                    console.log(res)
                    this.setState(state => state.usersInfo.push(...res.data.response.items));
                    offsetFriendsInterval += 100;
                })
                .catch(e => console.log('ERR getFriends', e));
        }, 2000);
    }

    prev (e) {
        clearInterval(this.subscribersInterval);
        clearInterval(this.friendsInterval);
        this.props.go(e);
    }

    componentDidMount () {
        this.getFriends();
    }

    render () {
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    left={<HeaderButton onClick={this.prev.bind(this)} data-to="home">
                        {this.osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                    </HeaderButton>}
                >
                    Друзья
                </PanelHeader>
                <Group title={`Друзья в инстаграме ${this.state.usersInfo.length}`}>
                    {this.state.error}
                    {
                        this.state.isSpinner &&
                        <Cell size="l">
                            <Spinner size="large" style={{margin: '150px 0'}}/>
                        </Cell>
                    }
                    {
                        this.state.usersInfo.map((friend, index) => {
                            return (
                                <div key={index}>
                                    <Cell
                                        size="l"
                                        description={friend.status || ''}
                                        before={<Avatar src={friend.photo_200}/>}
                                    >
                                        {`${friend.first_name} ${friend.last_name}`}
                                        {friend}
                                    </Cell>
                                </div>
                            );
                        })
                    }
                </Group>
            </Panel>
        );
    }
}

VueFriends.propTypes = {
    go: PropTypes.func.isRequired,
    access_token: PropTypes.string.isRequired
};

export default VueFriends;
